import React, { useState, useEffect } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
import { getAppUser } from '../../handlers/Api/Calls';
import { ErrorToast, Loading } from "../../components";
import { imageFromBase64 } from '../../handlers/Utils';
import { UserTags } from './components/UserTags';
import { AllTags } from './components/AllTags';

const User = props => {
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    getAppUser(props.match.params.id,
      (apiLoading, apiData, apiError) => {
        setData(apiData);
        setError(apiError);
        setIsLoading(apiLoading);
      }
    );
  }, [props.match.params.id]);

  const userDetails = data ? Object.entries(data) : [['id', (<span><i className="text-muted icon-ban"></i> Not found</span>)]]

  return (
    <div className="animated fadeIn">
      {error && <ErrorToast error={error} />}
      <Row>
        <Col lg={6}>
          <Card>
            <CardHeader>
              <strong><i className="icon-info pr-1"></i>User details</strong>
            </CardHeader>
            <CardBody>
              {isLoading ? <Loading /> :
                <Table responsive striped hover>
                  <tbody>
                    {userDetails.map(([key, value]) => {
                      if (key === 'image') {
                        return (
                          <tr key={key}>
                            <td>{`${key}:`}</td>
                            <td>
                              <img
                                src={imageFromBase64(value)}
                                width={40}
                                height={40}
                                style={{ borderRadius: 100 }}
                                alt="user"
                              />
                            </td>
                          </tr>
                        )
                      }
                      return (
                        <tr key={key}>
                          <td>{`${key}:`}</td>
                          <td><strong>{value}</strong></td>
                        </tr>
                      )
                    })}
                  </tbody>
                </Table>}
            </CardBody>
          </Card>
        </Col>
        <Col lg={6}>
          <UserTags id={props.match.params.id} />
        </Col>
        <Col lg={12}>
          <AllTags id={props.match.params.id} />
        </Col>
      </Row>
    </div>
  )
}

export default User;
