import React, { useState } from 'react';
import { followTags } from '../../../handlers/Api/Calls';
import { ErrorToast, Loading } from '../../../components';
import { Button } from 'reactstrap';

export const TagItem = ({ userId, tag }) => {
    const [data, setData] = useState([]);
    const [error, setError] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const [success, setSuccess] = useState(false);
  
    const followTag = tagId => {
      followTags(userId, [{ "id": tagId }],
        (apiLoading, apiData, apiError, apiSuccess) => {
          setData(apiData);
          setError(apiError);
          setIsLoading(apiLoading);
          setSuccess(apiSuccess);
        }
      );
    }
  
    return (
      <Button
        className="tag"
        onClick={() => followTag(tag.id)}
        block
        color={tag.follow || success ? 'primary' : 'secondary'}
      >
        {error && <ErrorToast error={error} />}
        {isLoading ? <Loading type="border" size="sm" /> :
          <>
            <i className={`tag-icon ${tag.icon}`}></i>
            <span className="tag-text">{tag.name}</span>
          </>}
      </Button>
    )
  }