import React, { useState, useEffect } from 'react';
import { Card, CardBody, CardHeader, Col, Row } from 'reactstrap';
import { Loading, ErrorToast } from '../../../components';
import { getUserTags } from '../../../handlers/Api/Calls';
import { TagItem } from './TagItem';

export const UserTags = ({ id }) => {
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    getUserTags(id,
      (apiLoading, apiData, apiError) => {
        setData(apiData);
        setError(apiError);
        setIsLoading(apiLoading);
      }
    );
  }, [id]);

  return (
    <Card>
      <CardHeader>
        <strong><i className="icon-info pr-1"></i>User tags</strong>
      </CardHeader>
      <CardBody>
        {error && <ErrorToast error={error} />}
        <Row>
          <Col lg={3}>
            {isLoading ? <Loading /> :
              data && data.map((value) => {
                return (
                  <TagItem userId={id} tag={value} />
                )
              })
            }
          </Col>
        </Row>
      </CardBody>
    </Card>
  )
}