import React, { useState, useEffect } from 'react';
import { Card, CardBody, CardHeader, Col, Row } from 'reactstrap';
import { Loading, ErrorToast } from '../../../components';
import { getAllUserTags } from '../../../handlers/Api/Calls';
import { TagItem } from './TagItem';

export const AllTags = ({ id }) => {
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    getAllUserTags(id,
      (apiLoading, apiData, apiError) => {
        setData(apiData);
        setError(apiError);
        setIsLoading(apiLoading);
      }
    );
  }, [id]);

  return (
    <Card>
      <CardHeader>
        <strong><i className="icon-info pr-1"></i>All tags</strong>
      </CardHeader>
      <CardBody>
        {error && <ErrorToast error={error} />}
        <Row>
          {isLoading ? <Loading /> :
            data && data.map((value) => {
              return (
                <Col lg={3}>
                  <TagItem userId={id} tag={value} />
                </Col>
              )
            })
          }
        </Row>
      </CardBody>
    </Card>
  )
}
